body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    position: fixed;
    width: 100%;
    z-index: 1;
    box-shadow: 2px 2px 15px slategrey;
}

ul {
    width: 100%
}

li {
    float: left;
    width: 33%;
    text-align: center;

}
h1, h3, p {
    text-align: center;
}

h1, h3 {
    font-family: 'Alfa Slab One', cursive;
    color: white;
    text-shadow: 3px 3px 2px slategrey;
}

.lead {
    color: white;
    font-weight: bold;
    text-shadow: 3px 3px 2px slategrey;
}
.jumbotron {
    background-image: url(/clicky-game/static/media/parksandrec.946331dc.jpg);
    box-shadow: 3px 3px 10px slategrey
}
img {
    width: 200px;
    height: 200px;
    border-width: 2px;
    border-color: black;
}

.card {
    float: left;
    margin: 10px;
}
